<template>
  <div class="upload">
    <el-upload
        class="upload-demo"
        ref="upload"
        :on-success="handleUploadSuccess"
        action="https://maoyou.site/wallpaper/upload"
        :file-list="fileList"
        :auto-upload="false">
      <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
      <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
      <div slot="tip" class="el-upload__tip">只能上传图片文件</div>
    </el-upload>
  </div>

</template>

<script>

export default {
  data() {
    return {
    };
  },
  methods: {
    submitUpload() {
      this.$refs.upload.submit();
    },
    handleUploadSuccess(response){
      if(response==1)
        this.success1();
      else if(response==2)
        this.error2();
      else
        this.error3();
    },
    success1(){
      this.$message({
        showClose: true,
        message: '文件上传成功',
        type: 'success'
      });
    },
    error2() {
      this.$message({
        showClose: true,
        message: '文件已存在',
        type: 'error'
      });
    },
    error3() {
      this.$message({
        showClose: true,
        message: '错误',
        type: 'error'
      });
    },
  }
}
</script>

