<template>
  <div id="container">
    <el-row>
      <el-col :span="5" v-for="o in this.ImageData" :key="o" :offset="1">
        <el-card :body-style="{ padding: '0px'}" style="margin-bottom: 20px;">
          <el-image style="height: 200px;" fit="fit"
              :src=o.minUrl class="image">
          </el-image>
          <div style="padding: 14px;">
            <div class="bottom clearfix">
              <p class="name">id: {{ o.id }}</p>
              <el-button type="primary">修改标签</el-button>
              <el-button type="danger" plain class="button">删除</el-button>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
    <div class="block">
      <el-pagination
          background
          @current-change="handleCurrentChange"
          :page-size="8"
          layout="prev, pager, next, jumper"
          :total= TotalNum>
      </el-pagination>
    </div>
  </div>
</template>

<script>

import axios from "axios";

export default {
  name: 'VmImageList',
  components: {

  },
  methods:{
    getImageList(p,l){
      axios.get("https://maoyou.site/wallpaper/list",{
        params:{
          pcursor: p,
          limit: l
        }
      }).then((res)=>{
        console.log(res.data.list);
        this.ImageData = res.data.list;
      }).catch((err)=>{
        console.log(err);
      })
    },
    getTotalNum(){
      axios.get("https://maoyou.site/wallpaper/totalNum").then((res)=>{
        this.TotalNum = res.data;
      }).catch((err)=>{
        console.log(err);
      })
    },
    handleCurrentChange(val){
      this.getImageList(val,8);
    }
  },
  mounted() {
    this.getImageList(1,8);
    this.getTotalNum()
  },
  // origin data
  data() {
    return {
      currentDate: new Date(),
      ImageData: [],
      TotalNum: 8,
    };
  }

}
</script>

<style>
.name {
  font-size: 16px;
  margin: 0;
  color: #0a0808;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  margin-top: 10px;

}

.image {
  width: 100%;
  display: block;
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}

.clearfix:after {
  clear: both
}


</style>